import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
//import Hello from '@/components/Hello'

import commission from '@/components/commission_details'
import order_list from '@/components/order_list'

Vue.use(Router)
Vue.use(Resource)

//require (['./components/order_list'],resolve);
export default new Router({
	mode:"history",
	base:__dirname,
	routes: [
		{ path: '/', component: commission },
		{ path: '/commission_details', name: "commission_details", component: commission },
		{ path: '/order_list', name: "order_list", component: function(resolve){
			require(['@/components/order_list'],resolve);
		} }
	]
})


